p,
h1,
h2 {
  margin: 0;
}

h2 {
  font-weight: 400;
}

.page {
  font-family: Inter, sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
  color: #1c1a26;

  scroll-behavior: smooth;
}

.page__body {
  background-color: #eef7ff;

  margin: 0;
}

.page__menu {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;

  opacity: 0;
  pointer-events: none;
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
}
